import React from 'react';
import DataContent from './dataContent';
import { Data } from '../storedData';
import Modal from './modal';
import Pricing from './pricing';
import {IntlProvider,FormattedMessage} from 'react-intl';
import {messages} from '../translations/messages';

class Body extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            ...Data,
            language: props.language,
            currentElement: undefined
        }
    }
    componentDidMount(){
        let val = this.state.data;
        val.sort(function(a,b){
            let time = new Date().getTime();
            let adiff = Math.floor((time - a.createdOn)/(1000 * 3600 * 24));
            let bdiff = Math.floor((time - b.createdOn)/(1000 * 3600 * 24));
            return adiff - bdiff;
        });
        this.setState({data:val});
    }
    changeActiveTab = (index) => {
        if(index<3 && index >= 0){
            let links = document.querySelectorAll('.tab-links');
            let tabs = document.querySelectorAll('.tab-content');
            if(links === undefined){
                console.log("Links Empty");
                return;
            }
            if(!links[index].classList.contains("active")){
                links.forEach((element,i) => {
                    element.classList.remove("active");
                    if(index === i)
                        element.classList.add("active");
                });
            }
            if(!tabs[index].classList.contains("active")){
                tabs.forEach((element,i) => {
                    element.classList.remove("active");
                    if(index === i)
                        element.classList.add("active");
                });
            }
        }
    }
    changeUpcomingTab = () => { this.changeActiveTab(0) }
    changeLiveTab = () => { this.changeActiveTab(1) }
    changePastTab = () => { this.changeActiveTab(2) }

    
    toggleModal = (modalName) => {
        let modal = document.querySelector("."+modalName)
        if(modal !== null)
            modal.classList.toggle("show-modal");
    }

    changeTimeStamp(element,timeStamp){ 
        let index = this.state.data.findIndex(e => e.name === element);
        if(index !== -1){
            let d = this.state.data;
            d[index].createdOn = timeStamp;
            let val = d;
            val.sort(function(a,b){
                let time = new Date().getTime();
                let adiff = Math.floor((time - a.createdOn)/(1000 * 3600 * 24));
                let bdiff = Math.floor((time - b.createdOn)/(1000 * 3600 * 24));
                return adiff - bdiff;
            });
            this.setState({data:val});
        }
    }
    setTimeStampToChange = (evt) => {
        this.setState({currentElement:evt.currentTarget.value});
        this.toggleModal("modal");
    }
    getTimeStampValueToChange = (evt) => {
        if(evt.currentTarget.getAttribute("value") !== null){
            let d = new Date(evt.currentTarget.getAttribute("value")).getTime();
            this.changeTimeStamp(this.state.currentElement,d);
        }
        this.toggleModal("modal");
    }
    
    formatDate(timeStamp){
        let d = new Date(timeStamp);
        let months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sept", "Oct", "Nov", "Dec"];
        let month = months[d.getMonth()];
        let year = d.getFullYear();
        let day = d.getDate();
        return month+' '+year+', '+day;
    }
    formatTimeLeft(createdTime){
        let time =  Math.floor((new Date().getTime() - createdTime)/(1000 * 3600 * 24))
        if(time > 0)
            return time+" Days Ago";
        else
            return (-time)+" Days Left";
    }
    showPricingModal = (evt) => {
        this.setState({currentElement:evt.currentTarget.value});
        this.toggleModal("price-modal");
    }
    getPricingElement(element){
        if(element === undefined) return;
        let index = this.state.data.findIndex(e => e.name === element);
        return <Pricing gameData={this.state.data[index]} />;
    }
    render() {
        let pricing = this.getPricingElement(this.state.currentElement);
        let UpcomingDataResult = [];
        let LiveDataResult = [];
        let PastDataResult = [];
        let currentTimeStamp = new Date().getTime();
        this.state.data.forEach(element=>{
            let diff = Math.floor((currentTimeStamp - element.createdOn)/(1000 * 3600 * 24))
            let date = this.formatDate(element.createdOn);
            let duration = this.formatTimeLeft(element.createdOn);
            if(diff === 0)
                LiveDataResult.push(<DataContent key={element.name} language={this.props.language} date={date} duration={duration} gameData={element} lastChanged={this.state.currentTarget} showPrice={this.showPricingModal} changeTimestamp={this.setTimeStampToChange} />);
            else if(diff < 0)
                UpcomingDataResult.push(<DataContent key={element.name} language={this.props.language} date={date} duration={duration} gameData={element} lastChanged={this.state.currentTarget} showPrice={this.showPricingModal} changeTimestamp={this.setTimeStampToChange} />);
            else
                PastDataResult.push(<DataContent key={element.name} language={this.props.language} date={date} duration={duration} gameData={element} lastChanged={this.state.currentTarget} showPrice={this.showPricingModal} changeTimestamp={this.setTimeStampToChange} />);
        });
        return (
            <IntlProvider locale={this.props.language} key={this.props.language} messages={messages[this.props.language]}>
                <section>
                    <section className="adjust">
                        <div className="heading"><FormattedMessage id="manageCampaign" defaultMessage="Manage Campaigns" description="Manage Description Text"/></div>
                        <div className="tabs-section">
                            <button className="tab-links active" onClick={this.changeUpcomingTab}><FormattedMessage id="upcoming" defaultMessage="Upcoming" description="Upcoming Tab"/> <span className='small-hide'><FormattedMessage id="campaigns" defaultMessage="Campaigns" description="Campaigns"/></span></button>
                            <button className="tab-links" onClick={this.changeLiveTab}><FormattedMessage id="live" defaultMessage="Live" description="Live Tab"/> <span className="small-hide"><FormattedMessage id="campaigns" defaultMessage="Campaigns" description="Campaigns"/></span></button>
                            <button className="tab-links" onClick={this.changePastTab}><FormattedMessage id="past" defaultMessage="Past" description="Past Tab"/> <span className="small-hide"><FormattedMessage id="campaigns" defaultMessage="Campaigns" description="Campaigns"/></span></button>

                            <table className="tab-content active">
                                <thead><tr><td>Date</td><td>Campaign</td><td>View</td><td>Actions</td></tr></thead>
                                <tbody>{UpcomingDataResult}</tbody>
                            </table>
                            <table className="tab-content">
                                <thead><tr><td>Date</td><td>Campaign</td><td>View</td><td>Actions</td></tr></thead>
                                <tbody>{LiveDataResult}</tbody>
                            </table>
                            <table className="tab-content">
                                <thead><tr><td>Date</td><td>Campaign</td><td>View</td><td>Actions</td></tr></thead>
                                <tbody>{PastDataResult}</tbody>
                            </table>
                        </div>
                    </section>
                    <Modal timestamp={this.getTimeStampValueToChange}/>
                    {pricing}
                </section>
            </IntlProvider>
        )
    }
 }
 
 export default Body;