import React from 'react';
import './App.css';
import Header from './components/header';
import Body from './components/body';

class App extends React.Component {
  constructor(){
    super();
    this.state = {
      language: 'en',
      availableLanguages: [{value:'en',name:"English"},{value:'es',name:"Spanish"},{value:'fr',name:"French"},{value:'jp',name:"Japanese"}]
    }
  }
  changeLanguage = (evt) => {
    this.setState({language: evt.currentTarget.value});
  }
  render(){
    return (
      <div className="App">
        <Header language={this.state.language} availableLanguages={this.state.availableLanguages} changeLanguage={this.changeLanguage}/>
        <Body language={this.state.language}/>
      </div>
    );
  }
}

export default App;
