import React from 'react';
import {IntlProvider,FormattedMessage} from 'react-intl';
import {messages} from '../translations/messages';

class DataContent extends React.Component{
    downloadCSV = () => {
        window.location.href='/'+this.props.gameData.csv;
    }
    downloadReport = () => {
        window.location.href='/'+this.props.gameData.report;
    }
    render(){
        return(
            <IntlProvider locale={this.props.language} key={this.props.language} messages={messages[this.props.language]}>
            <tr>
                <td>
                    <div className="game-date">{this.props.date}</div>
                    <div className="game-duration">{this.props.duration}</div>
                </td>
                <td>
                    <div className="game-image"><img width="100%" height="auto" alt={this.props.gameData.name+" image"} src={process.env.PUBLIC_URL+this.props.gameData.image_url}/></div><div className="game-about"><div className="game-name">{this.props.gameData.name}</div><div className="game-region">{this.props.gameData.region}</div></div>
                </td>
                <td>
                    <button className="pricing" onClick={this.props.showPrice} value={this.props.gameData.name}><i className="fa fa-dollar"/><span className="small-hide"><FormattedMessage id="viewPrice" defaultMessage="View Pricing" description="View Pricing"/></span></button>
                </td>
                <td className="actions">
                    <div className="flex">
                        <button className="csv" onClick={this.downloadCSV}><i className="fa fa-file"/><span className="medium-hide">CSV</span></button>
                        <button className="report" onClick={this.downloadReport}><i className="fa fa-bar-chart"/><span className="medium-hide"><FormattedMessage id="report" defaultMessage="Report" description="Report"/></span></button>
                        <button className="re-schedule" onClick={this.props.changeTimestamp} value={this.props.gameData.name}><i className="fa fa-calendar"/><span className="medium-hide"><FormattedMessage id="scheduleAgain" defaultMessage="Schedule Again" description="Schedule Again"/></span></button>
                    </div>
                </td>
            </tr>
            </IntlProvider>
        );
    }
}

export default DataContent;