import React from 'react';

class Pricing extends React.Component{
    toggleModal = () => {
        let modal = document.querySelector(".price-modal");
        if(modal !== null)
            modal.classList.toggle("show-modal");
    }
    render(){
        let pricingValues = [];
        for(let key in this.props.gameData.price){
        pricingValues.push(<tr key={key}><td>{key}</td><td className="money">{this.props.gameData.price[key]}</td></tr>);
        }
        return (
            <div className="price-modal" onClick={this.toggleModal}>
                <div className="price-modal-content">
                    <div className="flex">
                        <div className="price-game-logo"><img width="100%" height="auto" alt={this.props.gameData.name+" image"} src={process.env.PUBLIC_URL+this.props.gameData.image_url}/></div>
                        <div className="price-game-about"><div className="game-name">{this.props.gameData.name}</div><div className="game-region">{this.props.gameData.region}</div></div>
                    </div>
                    <h3>Pricing</h3>
                    <table>
                        <tbody>{pricingValues}</tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default Pricing;