import React from 'react';

class Header extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            language: props.language,
        }
    }
    render() {
        let showLanguages = [];
        this.props.availableLanguages.forEach(element => {
            showLanguages.push(<option key={element.value} value={element.value}>{element.name}</option>);
        });
        return (
            <div id="header">
                <header>
                <ul className="left">
                    <li><a href={process.env.PUBLIC_URL}><img alt="brand-logo" height="30px" width="auto" className="header-logo" src={process.env.PUBLIC_URL+"/images/logo.png"}/></a></li>
                </ul>
                <ul className="right" style={{padding:"4px 6px",paddingRight:"20px"}}>
                    <select name="language" defaultValue={this.state.language} onChange={this.props.changeLanguage} style={{padding:"2px 6px",borderRadius:"15px"}}>
                        {showLanguages}
                    </select>
                </ul>
                </header>
            </div>
        )
    }
 }
 
 export default Header;