import React from 'react';

class Modal extends React.Component{
    setValue(evt){
        var timestamp = document.querySelector('input[name="timestamp"]');
        var set = document.querySelector(".modal-content .set-button");
        set.setAttribute("value",timestamp.value);
    }
    render(){
        return (
            <div className="modal">
                <div className="modal-content">
                    <div>Re-Schedule</div>
                    <div className="select-date"><input type="date" name="timestamp" onChange={this.setValue}/></div>
                    <div className="set-button" onClick={this.props.timestamp}><button>Set</button></div>
                </div>
            </div>
        );
    }
}

export default Modal;